@import (less) "color.less"; 
   
nav {
    background-color: @green;
    height: 50px; //3.125em; se font-size: 16px; 
    border-radius: 0;
}

.navbar-absolute-top {
    position: absolute;
    border: 0 !important;
    margin: 0 !important;
    top: 0;
    left: 0;
    right: 0;
}

.navbar {
    .container {
        display: table;
        
    }
    .navbar-header {
        display: table-row;
        height: 50px;
        .navbar-logo {
            vertical-align: middle;
            display: table-cell;
            height:50px;
        }
    }
} 